import * as React from 'react';
import MedicineSale from './MedicineSale';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import styled from 'styled-components';
import { useState, useEffect } from 'react'

import axios from 'axios';


export default function MedicineSaleDialog(props)
{

    const [bankList, setBankList] = useState([])
  const [cashList, setCashList] = useState([])
  const [medicineSale, setMedicineSale] = useState([])
 
  const [showBank,setShowBank]=useState(false)
  const [showCash,setShowCash]=useState(false)


  const [amount, setAmount] = useState('')

  const [relatedBankAcc, setRelatedBankAcc] =useState('')
  const [relatedCashAcc, setRelatedCashAcc] =useState('')
   
  
    const [ remark, setRemark] = useState('');
   

  
   //setting up for data 


  const [relatedAccounting,setRelatedAccounting]=useState('')

  
    const MedicineSaleCreate = () => { 
      const jsonData = {
         remark: remark,relatedAccounting:relatedAccounting,amount:props.grand,date:Date.now()
      }
        if (relatedCashAcc) jsonData.relatedCash = relatedCashAcc
    if (relatedBankAcc) jsonData.relatedBank = relatedBankAcc
      const config = {
        headers: { 'Content-Type': 'application/json' }
      }
      axios.post('http://clinicdenovobackend.kwintechnologies.com:3000/api/medicine-sales/transaction', jsonData, config)
        .then(function (response) {
          alert(response.data)
        })
        .catch(function (err)
        {
          alert(err)
        })
        props.setOpen(false)
    }
  const handleBankRadioChange = () => {
    setShowBank(true)
    setShowCash(false)
  }

  const handleCashRadioChange = () => {
    setShowBank(false)
    setShowCash(true)
  }

  
useEffect(()=> {
  
    // const getCashLists = async () =>{
    //     try{
    //       const res = await axios.get('http://clinicdenovobackend.kwintechnologies.com:3000/api/accounting-lists');
          
    //       const cash= res.data.list.filter((el)=>el.name=='Main Cash Book');
    //       setCashList(cash);
    //     }catch(err){
          
    //     }
    //   };
      const getAccountingLists = async () =>{
        try{
          const res = await axios.get('http://clinicdenovobackend.kwintechnologies.com:3000/api/accounting-lists');
          const medicineSale = res.data.list.filter( (e) => e.name == 'Treatment Sale Credit');
          setAmount(medicineSale[0].amount);
          
          setMedicineSale(medicineSale)

          setBankList(res.data.list.filter((e) => e.accountingTypes == 'Current Assets'))
           const cash= res.data.list.filter((el)=>el.name=='Main Cash Book');
          setCashList(cash);
        }catch(err){
          
        }
    };

    // getBankLists();
  
    getAccountingLists();

  },[]);

  return (
    <div>

      <Dialog open={props.open} onClose={props.close}>
      <DialogTitle>   <div className="modal-header bg-info">
            <h4 className="modal-title">Medicine Sale</h4>
                  <button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={props.close}>
                    <span aria-hidden="true">&times;</span>
                  </button>
            </div></DialogTitle>
        <DialogContent>
          <DialogContentText>
          
          </DialogContentText>
        
          <form>
                    {/* @csrf */}
                    <div className="row offset-md-4">
                        <div className="col-md-2">
                        <div className="form-check form-check-inline mt-2 ">
                            <input className="form-check-input" type="radio" name="bank_cash" id="bank" value="1" onClick={handleBankRadioChange}/>&nbsp;
                            <label className="form-check-label text-success" for="bank">Bank</label>
                          </div>
                        </div>
                        <div className="col-md-2">
                          <div className="form-check form-check-inline mt-2 px-4">

                            <input className="form-check-input" type="radio" name="bank_cash" id="cash" value="2" onClick={handleCashRadioChange}/>
                            <label className="form-check-label text-success" for="cash">Cash</label>
                        </div>
                        </div>
                    </div>

                    {showBank && (
                            <div className="form-group mt-3" id="bankkk">
                            <label className="control-label">Bank Account</label>
    
                            <select className="form-control" name="bank_acc" id="bank_acc" onChange={(e) => setRelatedBankAcc(e.target.value)}>
                                <option value="">Select Bank Account</option>
                               {/* @foreach ($account as $acc) */}
                               {bankList.map((option) => (
                  <option value={option._id}>{option.name}</option>
                ))}
    
                               {/* @endforeach */}
                            </select>
                        </div>
                    )}

                   {showCash && (
                         <div className="form-group mt-3" id="cashhh">
                         <label className="control-label">Cash Account</label>
 
                         <select className="form-control" name="cash_acc" id="cash_acc" onChange={(e) => setRelatedCashAcc(e.target.value)}>
                             <option value="">Select Cash Account</option>
                            {/* @foreach ($cash_account as $acc) */}
                            {cashList.map((option) => (
               <option value={option._id}>{option.name}</option>
             ))}
 
                            {/* @endforeach */}
                         </select>
                     </div>
                   )}


                            <div className="form-group mt-3">
                                <label className="control-label">Incoming Account</label>
                                <select className="form-control" name="exp_acc" onChange={(e) => setRelatedAccounting(e.target.value)}>
                                    <option value="">Select Incoming Account</option>
                                 

                                   {medicineSale.map((option) => (
              <option value={option._id}>{option.name}</option>
            ))}

                                </select>
            </div>

                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label className="control-label">Grand Total</label>

                                        <input type="number" className="form-control" name="amount" value={props.grand} defaultValue={props.grand} />

                                    </div>
                                </div>
                                <div className="col-md-6">
                                   
                            <div className="form-group">
                                <label className="control-label">Remark</label>
                                <input type="text" className="form-control" name="remark" onChange={(e) => setRemark(e.target.value)}/>
                            </div>
                                </div>
                            </div>

                          

                           
                                <div className="row">
                                    <div className="col-md-9 mt-4">
                                        <button type="submit" className="btn btn-success" onClick={MedicineSaleCreate} >Submit</button>
                                        <button type="button" className="btn btn-inverse btn-dismiss" data-dismiss="modal" onClick={props.close}>Cancel</button>
                                    </div>
                                </div>

          </form>

        </DialogContent>
        

      </Dialog>
 
    </div>
  );
}
